*{
  box-sizing: border-box;
}
body{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: cadetblue;
}

.btn{
background-color: aliceblue;
color: yellow;
font-family: sans-serif;
text-transform: uppercase;
margin: 25;
padding: 15px;
border: 2x solid rgba(255, 255, 0, 0.493);
border-radius: 5px;
box-shadow: 0 12px #949015, 0 18x rgb(0, 0, 0,0.4 ) ;
  font-size: 60px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  position: relative;
  top: 0; 

}

.btn:hover{
  background-color: #e5dc15;
  color: #0e80b0;
  top: 4px;
  box-shadow: 0 8px #c2bb11, 0 14x rgb(0, 0, 0,0.4 ) ;
}

.btn:active{
  top: 8px;
  box-shadow: 0 4px #c2bb11, 0 10x rgb(0, 0, 0,0.4 ) ;
}

.game{
  display: flex;
  flex-wrap: wrap;
}
.game-info{
  margin: 15px;
  color: white;
}
.board> div{
display: flex;
}